.popup {
  background-color: rgb(0,0,0,.5);
  align-items: center;
  display: flex;
  justify-content: center;
  left: 0;
  top: 0;
  margin: 0;
  height: 100%;
  padding: 0;
  position: fixed;
  width: 100%;
  visibility: hidden;
  opacity: 0;
  transition: visibility .3s, opacity .3s;
}